@import '../../lib/sass/px2rem';
.the_toast_container { position: fixed; top: 0; right: 0; bottom: 0; left: 0; z-index: 2; background: url(data:image/png;base64,) repeat; text-align: center; font-size: 0;
    &:after { content: ''; display: inline-block; width: 0; height: 80%; vertical-align: middle;}
    .the_toast_msg { width: q(587); display: inline-block; border-radius: q(18); background-color: #f1f3f6; color: #333; font-size: q(28); line-height: 1.4; text-align: center; padding: q(26);
    }
}
